"use strict";

// 原型
const Plugin = {};
// 全局实例对象
import Vue from "vue";
// 引入主板模块
const echarts = require("echarts/lib/echarts");
// 引入折线图
require("echarts/lib/chart/line");
// 引入所需组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/markArea");
require("echarts/lib/component/visualMap");
// 开始封装图表
Plugin.install = function(Vue) {
  Object.defineProperties(Vue.prototype, {
    $chart: {
      get() {
        return {
          // 实例化图表 
          line: (id, data) => {
            // 基于准备好的dom，初始化echarts实例
            const myChart = echarts.init(document.getElementById(id));
            // 设置配置项
            const option = {
              grid: {
                left: 10,
                right: 40,
                bottom: 0,
                top: 20,
                containLabel: true,
              },
              tooltip: {
                trigger: "axis",
                axisPointer: {
                  type: "cross",
                },
              },
              formatter: function(params) {
                //格式化函数
                var tip = "";
                if (params != null && params.length > 0) {
                  tip += params[0].name + "<br/>";
                  if (params.length == 1) {
                    tip += params[0].marker  + '当前数值 : '+ params[0].data;
                  }
                  if (params.length == 2) {
                    const marker = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:RGB(202,112,205);"></span>' // 更改小圆圈
                    tip += params[0].marker  + '当前数值 : '+ params[0].data + '<br/>' + marker  + '当前数值 : '+ params[1].data ;
                  }
                }
                return tip;
              },
              xAxis: {
                type: "category",
                boundaryGap: false,
                data: data.times,
              },
              yAxis: {
                type: "value",
                axisPointer: {
                  snap: true,
                },
              },
              visualMap: {
                show: false,
                dimension: 0,
              },
              series: [
                {
                  name: "当前数值",
                  type: "line",
                  smooth: true,
                  areaStyle: {
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                      {
                        offset: 1,
                        color: "RGB(255,249,185)",
                      },
                    ]),
                  },
                  itemStyle: {
                    normal: {
                      color: "#FABF83", //改变折线点的颜色
                      lineStyle: {
                        color: "#FABF83", //改变折线颜色
                      },
                    },
                  },
                  data: data.parameters.length > 1 ? data.data[data.parameters[0]] : data.data,
                  markArea: {
                    data: [
                      [
                        {
                          name: "预测",
                          xAxis: data.earlyTimes[0],
                          itemStyle: {
                            color: data.isAlarm == true ? "#FF7F7F" : "#BEF8AA", // #BEF8AA #FF7F7F
                          },
                          label: {
                            show: true,
                            offset: [0, 0], // markArea中文字（name）显示在位置基础上x、y轴偏移
                            color: "black", // markArea中文字（name）颜色
                          },
                        },
                        {
                          xAxis: data.earlyTimes[data.earlyTimes.length - 1],
                        },
                      ],
                    ],
                  },
                },
                {
                  //  图表里面展示两条线 温度和湿度
                  name: "当前数值",
                  type: "line",
                  smooth: true,
                  areaStyle: {
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                      {
                        offset: 1,
                        color: "RGB(202,112,205)",
                      },
                    ]),
                  },
                  itemStyle: {
                    normal: {
                      color: "RGB(202,112,205)", //改变折线点的颜色
                      lineStyle: {
                        color: "RGB(202,112,205)", //改变折线颜色
                      },
                    },
                  },
                  data: data.parameters.length > 1 ? data.data[data.parameters[1]] : "",
                  markArea: {
                    data: [
                      [
                        {
                          name: data.parameters.length > 1 ? "预测" : "",
                          xAxis: data.parameters.length > 1 ? data.earlyTimes[0] : "",
                          itemStyle: {
                            color: data.isAlarm == true ? "#00FF7F" : "#BEF8AA", // #BEF8AA #FF7F7F
                          },
                          label: {
                            show: true,
                            offset: [0, 0], // markArea中文字（name）显示在位置基础上x、y轴偏移
                            color: "black", // markArea中文字（name）颜色
                          },
                        },
                        {
                          xAxis:  data.parameters.length > 1 ? data.earlyTimes[data.earlyTimes.length - 1] : '',
                        },
                      ],
                    ],
                  },
                },
              ],
            };
            // 生成图表
            myChart.resize();
            myChart.setOption(option);
          },
        };
      },
    },
  });
};

Vue.use(Plugin);

export default Plugin;
